.nav {
  position: fixed;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 60px;
  z-index: 100;
  transition: all 0.5s ease-in;
}

.nav_contents {
  display: flex;
  justify-content: space-between;
}

.nav_black {
  background-color: #000;
}

.nav_logo {
  position: fixed;
  top: 10px;
  left: 0;
  width: 80px;
  object-fit: contain;
  padding-left: 20px;
  cursor: pointer;
}

.nav_avatar {
  position: fixed;
  cursor: pointer;
  right: 20px;
  width: 30px;
}
